import React, { Component } from "react";
import { connect } from "react-redux";
import { getBooks } from "../actions/bookActions";
import PropTypes from "prop-types";
import {
  CLink,
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CDataTable
  } from '@coreui/react'
class Library extends Component {
  componentDidMount() {
    this.props.getBooks();
  }
  onDeleteClick = id => {
    this.props.deleteBook(id);
  };

  render() {
    const { books } = this.props.book;

    return (
      <>
       
          <CCard>
            <CCardHeader>
            <center><h4> - <small>    </small></h4></center>
            </CCardHeader>
            <CCardBody>
            <CDataTable
              items={books}
              fields={fields}
              striped
              columnFilter
              tableFilter
              footer
              itemsPerPageSelect
              itemsPerPage={5}
              hover
              sorter
              pagination
              scopedSlots = {{
                'id':
                  (item)=>(
                    <td>
                      <CLink 
                        href={`/viewBook/${item.id}`}
                        rel="noreferrer noopener" 
                        className="card-header-action"  >
                        <CButton block className=".btn-ghost-button" size="sm" color="success">View</CButton>
                      </CLink>
                      <CLink 
                        href={`/exportBook/${item.id}`}
                        rel="noreferrer noopener" 
                        className="card-header-action"  >
                        <CButton block className=".btn-ghost-button" size="sm" color="warning">Export</CButton>
                        </CLink>
                      
                    </td>
                    
                  )

              }}
            />
            </CCardBody>
          </CCard>
        
      </>
    );
  }
}
const fields = ['bookName', 'bookEdition', 'author', 'pageNumber', 'subject', 'subSubject', 'reference', 'actor', 'village', 'birthDate', 'birthDateGuj', 'deathDate', 'deathDateGuj',  'eventDate', 'eventDateGuj', 'id'];

Library.propTypes = {
  book: PropTypes.object.isRequired,
  getBooks: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  book: state.book
});

export default connect(
  mapStateToProps,
  { getBooks }
)(Library);