import React from "react";
import * as htmlToImage from 'html-to-image';
import jsPDF from 'jspdf';
import { CButton, CRow, CCol } from '@coreui/react'
const ExportToDoc = (data) => {
  const toggleFade = () => {
    window.history.back();
  };

  const ExportToWord = () => {
    var element = "exportContent";
    var header =
      "<html lang='en'><head><meta charset='utf-8'><meta name='viewport' content='width=device-width, initial-scale=1, shrink-to-fit=no'><meta name='theme-color' content='#000000'><link rel='manifest' href='/manifest.json'><link rel='shortcut icon' href='/favicon.ico'><link rel='stylesheet' href='https://use.fontawesome.com/releases/v5.2.0/css/all.css' integrity='sha384-hWVjflwFxL6sNzntih27bfxkr27PmbbK/iSvJ+a4+0owXq79v+lsFkW54bOGbiDQ' crossorigin='anonymous'><script nomodule src='https://cdnjs.cloudflare.com/ajax/libs/babel-polyfill/6.26.0/polyfill.min.js' integrity='sha256-WRc/eG3R84AverJv0zmqxAmdwQxstUpqkiE+avJ3WSo=' crossorigin='anonymous'></script><title> </title></head><body>";

    var footer = "</body></html>";

    var html = header + document.getElementById(element).innerHTML + footer;

    var blob = new Blob(["\ufeff", html], {
      type: "application/msword",
    });

    // Specify link url
    var url =
      "data:application/vnd.ms-word;charset=utf-8," + encodeURIComponent(html);

    // Specify file name
    var filename = "";
    filename = filename ? filename + ".doc" : "document.doc";

    // Create download link element
    var downloadLink = document.createElement("a");

    document.body.appendChild(downloadLink);

    if (navigator.msSaveOrOpenBlob) {
      navigator.msSaveOrOpenBlob(blob, filename);
    } else {
      // Create a link to the file
      downloadLink.href = url;

      // Setting the file name
      downloadLink.download = filename;

      //triggering the function
      downloadLink.click();
    }

    document.body.removeChild(downloadLink);
    
  };
  
  
  const printPDF = () => {
    htmlToImage.toPng(document.getElementById('exportContent'), { quality: 0.95 })
        .then(function (dataUrl) {
          var link = document.createElement('a');
          link.download = 'my-image-name.jpeg';
          const pdf = new jsPDF();
          const imgProps= pdf.getImageProperties(dataUrl);
          const pdfWidth = pdf.internal.pageSize.getWidth();
          const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
          pdf.addImage(dataUrl, 'PNG', 0, 0,pdfWidth, pdfHeight);
          pdf.save("download.pdf"); 
        });
  };
    
  
  return (
    <div id="exportContent">
      <table className="input-table">
        <tr>
          <th></th>
          <th></th>
          <th> </th>
          <th> </th>
          <th> </th>
          <th> </th>
        </tr>
        <tr>
          <td>{data.bookName}</td>
          <td>{data.bookEdition}</td>
          <td>{data.pageNumber}</td>
          <td>{data.eventDate}</td>
          <td>{data.eventDateGuj}</td>
          <td>{data.author}</td>
        </tr>
      </table>
      <hr/>
      <table className="input-table">
        <tr>
          <th></th>
          <th></th>
          <th>  </th>
          <th>  </th>
          <th>  </th>
          <th>  </th>
        </tr>
        <tr>
          <td>{data.actor}</td>
          <td>{data.village}</td>
          <td>{data.birthDate}</td>
          <td>{data.birthDateGuj}</td>
          <td>{data.deathDateGuj}</td>
          <td>{data.deathDateGuj}</td>
        </tr>
      </table>
      <hr/>
      <table className="table">
      <tr class="table-row">
      <td class="table-cell"><b> : </b></td><td class="table-cell">{data.subject}</td>
      </tr>
      <tr class="table-row">
      <td class="table-cell"><b> : </b></td><td class="table-cell">{data.subSubject}</td>
      </tr>
      <tr class="table-row">
      <td class="table-cell"><b>: </b></td><td class="table-cell">{data.reference}</td>
      </tr>
      <tr class="table-row">
      <td class="table-cell"><b> : </b></td><td class="table-cell">{data.comments}</td>
      </tr>
      <tr class="table-row">
      <td class="table-cell"><b>/: </b></td><td class="table-cell">{data.shlok}</td>
      </tr>
      </table>
 
      
      <h4>
        <center><label></label></center>
      </h4>
     
      <p>{data.event}</p>
      <hr/>
      <center>
      <CRow>
      <CCol md="4"></CCol>

        <CCol md="1">
          <CButton onClick={ExportToWord} color="info">Word</CButton>
        </CCol>
        <CCol md="1">
        <CButton onClick={printPDF} color="warning">PDF</CButton>
        </CCol>
        <CCol md="2">
          <CButton color="primary" onClick={toggleFade}>  </CButton>
        </CCol>
        <CCol md="3"></CCol>

      </CRow>
      </center>
    </div>
  );
};

export default ExportToDoc;

