import React, { Component } from "react";
import { getBook } from "../../actions/bookActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import ExportToDoc from "../ExportToDoc";

class ViewBook extends Component {
  //set state
  constructor() {
    super();

    this.state = {
      bookName: "",
      bookEdition: "",
      pageNumber: "",
      subject: "",
      subSubject: "",
      shlok: "",
      event: "",
      reference: "",
      actor: "",
      author: "",
      village: "",
      eventDate: "",
      birthDate: "",
      deathDate: "",
      eventDateGuj: "",
      birthDateGuj: "",
      deathDateGuj: "",
      comments: "",
      errors: {}
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
    const {
      id,
      bookName,
      bookEdition,
      pageNumber,
      subject,
      subSubject,
      shlok,
      event,
      reference,
      actor,
      author,
      village,
      eventDate,
      birthDate,
      deathDate,
      eventDateGuj,
      birthDateGuj,
      deathDateGuj,
      comments
    } = nextProps.book;

    this.setState({
      id,
      bookName,
      bookEdition,
      pageNumber,
      subject,
      subSubject,
      shlok,
      event,
      reference,
      actor,
      author,
      village,
      eventDate,
      birthDate,
      deathDate,
      eventDateGuj,
      birthDateGuj,
      deathDateGuj,
      comments
    });
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getBook(id, this.props.history);
  }
 
  render() {
   
      return (
        <ExportToDoc bookName={this.state.bookName} author={this.state.author} 
        bookEdition={this.state.bookEdition}  pageNumber={this.state.pageNumber} 
        subject={this.state.subject} subSubject={this.state.subSubject}
        shlok={this.state.shlok} event={this.state.event} 
        comments={this.state.comments} reference={this.state.reference} 
        actor={this.state.actor} village={this.state.village}
        eventDate={this.state.eventDate} eventDateGuj={this.state.eventDateGuj} 
        birthDate={this.state.birthDate} birthDateGuj={this.state.birthDateGuj} 
        deathDate={this.state.deathDate} deathDateGuj={this.state.deathDateGuj} />

    );
  }
}


ViewBook.propTypes = {
  getBook: PropTypes.func.isRequired,
  book: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  book: state.book.book,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getBook }
)(ViewBook);
