import React from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCardFooter,
  CRow,
  CCol,
  CLink
} from '@coreui/react'
import * as htmlToImage from 'html-to-image';
import jsPDF from 'jspdf';

const Popovers = (data) => {
 
  const toggleFade = () => {
    window.history.back();
  };

  const printPDF = () => {
    htmlToImage.toPng(document.getElementById('exportContent'), { quality: 0.95 })
        .then(function (dataUrl) {
          var link = document.createElement('a');
          link.download = 'my-image-name.jpeg';
          const pdf = new jsPDF();
          const imgProps= pdf.getImageProperties(dataUrl);
          const pdfWidth = pdf.internal.pageSize.getWidth();
          const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
          pdf.addImage(dataUrl, 'PNG', 0, 0,pdfWidth, pdfHeight);
          pdf.save("download.pdf"); 
        });
  };

  
  return (
    <div id="exportContent">
      <CCard>
        <CCardHeader>
        <CRow>
        <CCol><small>: </small> <label>{data.bookName}</label></CCol> 
        <CCol> <small>: </small> <label>{data.bookEdition}</label></CCol> 
        <CCol><small>  : </small><label>{data.pageNumber}</label></CCol>
       </CRow>
        </CCardHeader>
        <CCardBody>
          <CRow>
            <CCol md="0">
            <label className="text-left text-muted">:</label>
            </CCol>
            <CCol md="3">
            <text className="muted">{data.actor}</text>
            </CCol>
           
            <CCol md="0">
            <label className="text-left text-muted"> :</label>
            </CCol>
            <CCol md="3">
            <text className="muted">
            {data.birthDate} 
            </text>
            </CCol>
            <CCol md="0">
            <label className="text-left text-muted">  :</label>
            </CCol>
            <CCol md="3">
            <text className="muted">
            {data.birthDateGuj}
            </text>
            </CCol>
            
          
          </CRow>
          <CRow>
          <CCol md="0">
          <label className="text-left text-muted">:</label>
          </CCol>
          <CCol md="3"  className="text-left muted" >
          <text className="muted">{data.village}</text>
          </CCol>
          <CCol md="0">
            <label className="text-left text-muted"> :</label>
            </CCol>
            <CCol md="3">
            <text className="muted">
            {data.deathDate}
            </text>
          </CCol>
          <CCol md="0">
            <label className="text-left text-muted">  :</label>
            </CCol>
            <CCol md="3">
            <text className="muted">
              {data.deathDateGuj}
            </text>
            </CCol>
          </CRow>
        </CCardBody>
      </CCard>
      <CCard>
      <CCardHeader>
       <CRow>
        <CCol><small> : </small>  <label>{data.subject}</label></CCol> 
        <CCol><small> : </small><label>{data.subSubject}</label></CCol>
       </CRow>
       </CCardHeader>
       <CCardBody>
          <label className="text-muted"> : </label> 
          <text className="muted"> {data.reference}</text>
          <hr />
          <label className="text-muted">  : </label> 
          <text className="muted"> {data.comments}</text>
          <hr />
          <label className="text-muted"> /  : </label> 
        <text className="muted"> {data.shlok}</text>
       </CCardBody>  
      
    </CCard>
    
      <CCard>
        <CCardHeader>
        <CRow>
        <CCol  md="2"><label></label> </CCol> 
        <CCol  md="4"> <small> : </small> <label>{data.eventDate}</label></CCol> 
        <CCol  md="4"> <small>  : </small> <label>{data.eventDateGuj}</label></CCol> 
        <CCol  md="2"><small>  : </small><label>{data.author}</label></CCol>
       </CRow>
        </CCardHeader>
        <CCardBody>
       {data.event}
        </CCardBody>
        <CCardFooter>
        <center>
        <CRow>
        <CCol md="4"></CCol>

        <CCol md="1">
        <CLink 
        href={`/exportBook/${data.id}`}
        rel="noreferrer noopener" 
        className="card-header-action"  >
        <CButton color="info">Word</CButton>
        </CLink>
        </CCol>
        <CCol md="1">
        <CButton onClick={printPDF} color="warning">PDF</CButton>
        </CCol>
          <CCol  md="2">
          <CButton color="primary" onClick={toggleFade}>  </CButton>
          </CCol>
          <CCol md="4"></CCol>
          </CRow>
        </center>
      </CCardFooter>
      </CCard>
    </div>
  )
}

export default Popovers
