import React, { Component } from "react";
import CreateBookButton from "./Book/CreateBookButton";
import { connect } from "react-redux";
import { getMyBooks, deleteBook } from "../actions/bookActions";
import PropTypes from "prop-types";

import {
  CLink,
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
  } from '@coreui/react'
class Dashboard extends Component {
  componentDidMount() {
    this.props.getMyBooks();
  }
  onDeleteClick = id => {
    this.props.deleteBook(id);
  };
  render() {
    const { books } = this.props.book;

    return (
      
       <>
         
          <CCard>
            <CCardHeader>
            <CRow>
            <CCol>
              <center><h5> - <small>        </small></h5></center>
            </CCol>
              <CreateBookButton name="  +" />              
            </CRow>
            </CCardHeader>
            <CCardBody>
            <CDataTable
              items={books}
              fields={fields}
              striped
              itemsPerPage={5}
              pagination
              scopedSlots = {{
                'id':
                  (item)=>(
                    <td>
                      <CLink 
                        href={`/viewBook/${item.id}`}
                        rel="noreferrer noopener" 
                        className="card-header-action"  >
                        <CButton block className=".btn-ghost-button" size="sm" color="success">View</CButton>
                        </CLink>
                      <CLink 
                        href={`/updateBook/${item.id}`}
                        rel="noreferrer noopener" 
                        className="card-header-action"  >
                        <CButton block  className=".btn-ghost-button" size="sm" color="warning">Edit</CButton>

                      </CLink>
                      <CLink 
                      onClick={this.onDeleteClick.bind(
                        this,
                        item.id
                      )}
                        rel="noreferrer noopener" 
                        className="card-header-action"  >
                        <CButton block className=".btn-ghost-button" size="sm" color="danger">Delete</CButton>
                      </CLink>
         
            
                    </td>
                    
                  )

              }}
            />
            </CCardBody>
          </CCard>
        
      </>
            
    );
  }
}

const fields = ['bookName', 'bookEdition', 'author', 'pageNumber', 'subject', 'subSubject', 'reference', 'actor', 'village', 'birthDate', 'birthDateGuj', 'deathDate', 'deathDateGuj',  'eventDate', 'eventDateGuj', 'id'];

Dashboard.propTypes = {
  book: PropTypes.object.isRequired,
  getMyBooks: PropTypes.func.isRequired,
  deleteBook: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  book: state.book
});

export default connect(
  mapStateToProps,
  { getMyBooks, deleteBook }
)(Dashboard);